.grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0px 1fr 79px;
  grid-template-areas:
    "header"
    "main";
  height: 100vh;
}

.main-header {
  grid-area: header;
  background-color: #fff;
}

.main {
  grid-area: main;
  height: 100%;
  background-color: #fff;
}

.main-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  top: 0;
  height: 79px;
  width: 100%;
  z-index: 101;
  position: fixed;
  padding: 0 129px 0 28px;
  background-color: #fff;
  box-shadow: 0px 0px 6px #00000014;
}

.sidenav-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  top: 0;
  height: 79px;
  width: 100%;
  z-index: 101;
