/*  Profile Call Overlay */

#radioInput{
  margin-left: -30px;
  margin-top: -4px;
  position: absolute;
  width: 20px;
  height: 20px;
}
.profile-call-label {
  display: block;
  position: relative;
  padding-left: 35px;
  padding-top: 3px;
  font-size: 14px;
  color: var(--text-color);
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
/*
.profile-call-label input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
*/
.profile-call-label .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 24px;
  width: 24px;
  background-color: #fff;
  border: 1px solid var(--text-color);
  border-radius: 50%;
}
.profile-call-label input:checked ~ .checkmark {
  background-color: #fff;
}
.profile-call-label .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.profile-call-label input:checked ~ .checkmark:after {
  display: block;
}
.profile-call-label .checkmark:after {
  top: 2px;
  left: 2px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: var(--text-color);
}
/*  End Profile Call Overlay */

/* Add Personnel Overlay */
.personnel-checkbox h5 {
  display: inline-block;
}
.checkbox-label {
  display: block;
  position: relative;
  padding-left: 24px;
}
.check-mark {
  position: absolute;
  top: 0;
  left: 0;
  height: 1.7rem;
  width: 1.7rem;
  border-radius: 0.4rem;
  border: 1px solid #336799;
}
.personnel-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.check-mark:after {
  content: "";
  position: absolute;
  display: none;
}
.checkbox-label input:checked ~ .check-mark:after {
  display: block;
}
.checkbox-label .check-mark:after {
  left: 6px;
  top: 3px;
  width: 3px;
  height: 7px;
  border: solid #336799;
  border-width: 0 1px 1px 0;
  -webkit-transform: rotate(45deg);
